-3d77430c>\n            Huawei 2018 Seed For The Future\n          </p></div></div> <p class=\"text-lg pt-2 text-gray-900\" data-v-3d77430c>\n        Awarded an all-expenses-paid-for trip to China (Beijing and Shenzhen)\n        and Hong Kong for two weeks as one of 10 selected Belgian Computer\n        Science students.\n      </p></div></div> <hr class=\"my-10\" data-v-3d77430c> <div data-v-3d77430c><p class=\"font-color-1 opacity-80 font-bold text-lg mb-2\" data-v-3d77430c>\n      üíº Experiences\n    </p> <div data-v-3d77430c><div class=\"grid grid-cols-5 md:grid-cols-7 md:gap-4\" data-v-3d77430c><div class=\"col-span-5\" data-v-3d77430c><p class=\"font-color-1 font-bold text-2xl\" data-v-3d77430c>Co-creator of Studium</p></div> <div class=\"flex flex-col col-span-2 justify-end\" data-v-3d77430c><p class=\"text-left md:text-right text-gray-500\" data-v-3d77430c>\n            Aug 2020 - Sept 2022\n          </p></div></div> <p class=\"text-gray-500\" data-v-3d77430c>Ghent University, Belgium</p> <p class=\"text-lg pt-2 text-gray-900\" data-v-3d77430c>\n        Together with 2 friends, I created Studium ‚Äî a knowledge-sharing\n        application for the students at Ghent University.\n        <br data-v-3d77430c><br data-v-3d77430c>\n        As co-creator, I was responsible for designing the application,\n        securing funding, hiring two computer science engineers for a summer\n        internship, setting priorities for the development team, and helping\n        develop the application.\n        <br data-v-3d77430c><br data-v-3d77430c>\n        Our code is opensourced\n        <a href=\"https://gitlab.com/vtkgent/studium\" class=\"underline\" data-v-3d77430c>here</a>.\n      </p></div> <div class=\"mt-10\" data-v-3d77430c><div class=\"grid grid-cols-5 md:grid-cols-7 md:gap-4\" data-v-3d77430c><div class=\"col-span-5\" data-v-3d77430c><p class=\"font-color-1 font-bold text-2xl\" data-v-3d77430c>\n            NLP Internship\n          </p></div> <div class=\"flex flex-col col-span-2 justify-end\" data-v-3d77430c><p class=\"text-left md:text-right text-gray-500\" data-v-3d77430c>\n            Jul 2021 - present\n          </p></div></div> <p class=\"text-gray-500\" data-v-3d77430c>Waylay, Belgium</p> <p class=\"text-lg pt-2 text-gray-900\" data-v-3d77430c>\n        I designed and built a machine learning-based semantic parser which\n        allows the user to interface with the Waylay system over natural\n        language.\n        <br data-v-3d77430c><br data-v-3d77430c>\n        This problem was particularly challenging because of the extremely low\n        amount of data available. I solved this by leveraging large\n        pre-trained language models (specifically,\n        <span class=\"italic\" data-v-3d77430c>GPT3</span>) together with\n        <span class=\"italic\" data-v-3d77430c>prompt engineering</span> to build an extremely\n        data-efficient semantic parser to translate natural language inputs\n        into a format digestable by the Waylay system.\n      </p></div> <div class=\"mt-10\" data-v-3d77430c><div class=\"grid grid-cols-5 md:grid-cols-7 md:gap-4\" data-v-3d77430c><div class=\"col-span-5\" data-v-3d77430c><p class=\"font-color-1 font-bold text-2xl\" data-v-3d77430c>\n            Serverless Cloud Engineering Internship\n          </p></div> <div class=\"flex flex-col col-span-2 justify-end\" data-v-3d77430c><p class=\"text-left md:text-right text-gray-500\" data-v-3d77430c>Aug - sept 2020</p></div></div> <p class=\"text-gray-500\" data-v-3d77430c>Waylay, Belgium</p> <p class=\"text-lg pt-2 text-gray-900\" data-v-3d77430c>\n        I was tasked with migrating a core piece of software to a serverless\n        solution (for this, we used <span class=\"italic\" data-v-3d77430c>openFaaS</span> ). I\n        worked in a large codebase, devised a migration plan, and created\n        proofs of concepts to validate my approach. The migration was\n        successful and is currently running in production. <br data-v-3d77430c><br data-v-3d77430c>\n        Additionally, I gained experience working in an agile startup.\n      </p></div> <div class=\"mt-10\" data-v-3d77430c><div class=\"grid grid-cols-5 md:grid-cols-7 md:gap-4\" data-v-3d77430c><div class=\"col-span-5\" data-v-3d77430c><p class=\"font-color-1 font-bold text-2xl\" data-v-3d77430c>\n            Vice-President &amp; Board-Member\n          </p></div> <div class=\"flex flex-col col-span-2 justify-end\" data-v-3d77430c><p class=\"text-left md:text-right text-gray-500\" data-v-3d77430c>2019 - 2020</p></div></div> <p class=\"text-gray-500\" data-v-3d77430c>VTK Ghent non-profit, Belgium</p> <p class=\"text-lg pt-2 text-gray-900\" data-v-3d77430c>\n        VTK Ghent non-profit is the student association for all engineering\n        students at Ghent University.\n        <br data-v-3d77430c><br data-v-3d77430c>\n        As vice-president, I was responsible for the management and wellbeing\n        of 35 active volunteers and 200 regular volunteers. We organized\n        sport, culture and job-related events for 2000+ students.\n        <br data-v-3d77430c><br data-v-3d77430c>\n        I am particularly proud of my initiative to set up a collaboration\n        with the University to help integrate international students at the\n        engineering faculty. Additionally, I played a vital role in a\n        construction project ment to secure the future of the organization\n        (currently still ongoing). For this, I had to regularly correspond\n        with the top-level of Ghent University as well as several major\n        Belgian companies.\n      </p></div> <div class=\"mt-10\" data-v-3d77430c><div class=\"grid grid-cols-5 md:grid-cols-7 md:gap-4\" data-v-3d77430c><div class=\"col-span-5\" data-v-3d77430c><p class=\"font-color-1 font-bold text-2xl\" data-v-3d77430c>\n            Data-Science Engineering Internship\n          </p></div> <div class=\"flex flex-col col-span-2 justify-end\" data-v-3d77430c><p cla